      ],
      pageIndex: 1,
      listHeight: 100,
      loadingStatus: 'hide',
      initData: false,
      suffix: '.net',
      isWeb: false,
      noData: false,
      pageSize: 10,
      onePixelBorder: 1,
      noticeObj: {
        visibility: false,
        title: '退款成功'
      },
      categoryTypeParam: 0, // 分类ajax参数值
      categoryHeader: [{
        name: '全部',
        number: 0,
        show: true,
        param: 0
      }, {
